.App {
  text-align: center;
}

p {
  font-weight: bold;
}

/* strong {
  color: red;
} */

.logged-out-content img {
  margin-top: 10vh;
  width: 90%;
  max-width: 400px;
  height: auto;
}

.url-validation-error{
  color: red;
  margin: 0px auto;
  font-size: 10px;
  font-weight: bold;
  max-width: 785px;
  text-align: left;
}

.logged-out-header {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: rgba(255, 255, 255, .9);
  position: fixed;
  z-index: 2;
  width: 100%;
}

.logged-in-header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: rgba(255, 255, 255, .9);
  width: 100%;
}

.logged-out-content {
  background-color: rgba(255, 255, 255, 0.7);
  min-height: 100vh;
  height: 100%;
}

.logged-in-content {
  background-color: rgba(255, 255, 255, 0.7);
  padding-bottom: 50px;
  min-height: 100vh;
  height: 100%;
}

/* .header-logo {
  margin-left: 10px;
  height: 20px;
} */

.header-login-register {
  display: flex;
}

.long-input {
  width: 75%;
  max-width: 700px;
}

.original-url-container p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 30vw;
  min-width: 310px;
  margin: auto;
  max-width: 400px;
}

button {
  background-color: #cb4ed9;
  margin: 10px;
  border: none;
  box-shadow: 2px 2px black;
  border-radius: 2px;
}

button:hover {
  cursor: pointer;
  background-color: #43f063;
}